import { Component, OnInit } from '@angular/core';
import { ProductModel, ProductService } from '../products/product.service'
import { CartService } from '../shop/cart.service'

@Component({
    moduleId: module.id,
    selector: 'customersBtns',
    inputs: ['product'],
    template: `<button class="btn btn-warning"
                 (click) = "addToCart(product.id)"> 
                 <i class="glyphicon glyphicon-shopping-cart"></i> Add to cart
               </button>
                 <a routerLink="/product/{{product.id}}/{{product.name}}" class="btn btn-success">Details</a>`
})
export class CustomerBtnsComponent implements OnInit {
    
  private product: ProductModel;
    
  constructor(private cartService: CartService) { }
    
  ngOnInit() { }

  addToCart(productId : number) {
    
    this.cartService.addToCart(productId);
  }
}